<template>
<!--  -->
  <div class="register">
    <div class="form_container">
      <div class="manage_tip">
        <span class="title"></span>
        <el-form
          :model="register"
          :rules="rules"
          class="registerForm"
          ref="registerForm"
          label-width="80px"
        >
          <el-form-item label="" prop="email">
            <el-input
              type="email"
              v-model="register.email"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
            type="password"
            v-model="register.password"
            placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password2">
            <el-input
              type="password"
              v-model="register.password2" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input type="name" v-model="register.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
            class="submit_btn"
            type="primary"
            @click="submitForm('registerForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
// api
import { register } from '../api/userHT'
export default {
  name: 'register',
  data() {
    // 
    var checkPassword = (rule, value, callback) => {
      if (value !== this.register.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      register: {
        name: 'Robert',
        password: '123456',
        password2: '123456',
        email: '751135385@qq.com'
      },
      rules: {
        // 
        email: [{
          required: true, 
          message: '' 
        }],
        // 
        name: [{
          required:true,  //
          message: '', //
          trigger: 'blur' //     blur'
        }, {  //
          min: 2,
          max: 30,
          message: '230',
          trigger: 'blur'
        }],
        // 
        password: [{
          required:true,
          message: '',
        }, {
          min: 6,
          max: 30,
          message: '620',
          trigger: 'blur'
        }],
        // 
        password2: [{
          required:true,
          message: '',
        }, {
          validator: checkPassword,
          trigger: 'blur'
        }]
      }
    }
  },
  methods: {
    // ,
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          register(this.register)
          .then(res => {
            if (res) {
              this.$message({
                message: '',
                type: 'success'
              })
              this.$router.push("/login")
            }
          })
        } else {
          return false;
        }
      });
    }
  }
}
</script>
<style scoped>
  .register {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(../assets/bg.jpg) no-repeat center center;
  background-size: 100% 100%;
}
.form_container {
  width: 370px;
  height: 210px;
  position: absolute;
  top: 10%;
  left: 34%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.registerForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}

</style>
